@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #f5f5f5;
}

header {
  font-size: 2.5em;
  padding-bottom: 1em;
}

.typewriter-container {
  text-align: center;
}

.intro-h1 {
  font-size: 6em;
  font-weight: normal;
  margin-bottom: 0.5em;
}

.intro-h2 {
  font-size: 3em;
  font-weight: normal;
  margin-bottom: 0.5em;
}

.skills {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.card-container {
  background-color: white;
  max-width: 1920px;
  width: 70%;
  height: auto;
  border-radius: 14px;
  box-shadow: 0px 10px 30px hsl(185, 75%, 35%);

  .header-image {
    //background-image: url("../images/react-background.jpg");
    //background-color: white;
    background-position: 0px 0px;
    background-repeat: repeat;
    background-size: contain;
    text-align: center;
    border-top-left-radius: 14px;
    border-top-right-radius: 14px;
    padding: 5px;
  }

  img {
    margin: auto;
    width: 250px;
    border: solid #48d1cc 4px;
    border-radius: 50%;
    margin-top: 50px;
  }

  .name {
    font-weight: bold;
    font-size: 2em;
    text-align: center;
    padding: 10px;
  }

  .social-container {
    display: flex;
    border-top: solid #48d1cc 1px;
    text-align: center;
  }

  .about-me {
    border-top: solid #48d1cc 1px;
    text-align: center;
    .text {
      padding: 1em;
      font-size: 1.2em;
    }
  }

  .socials {
    padding: 1em;
    flex: 1;
  }

  .details {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  ul {
    list-style-type: none;
    margin-bottom: 1em;
    font-size: 1.5em;
  }
}

@media screen and (max-width: 1000px) {
  .intro-h1 {
    font-size: 4em;
  }

  .intro-h2 {
    font-size: 2em;
  }

  .card-container {
    img {
      width: 180px;
    }
    .name {
      font-size: 1.4em;
    }
    ul {
      font-size: 1em;
    }
    .about-me {
      .text {
        padding: 1em;
        font-size: medium;
      }
    }
  }
  .skills {
    flex-direction: column;
    svg {
      width: 135%;
      margin-left: -3em;
    }
  }
}

@media screen and (max-width: 400px) {
  .intro-h1 {
    font-size: 3em;
  }

  .intro-h2 {
    font-size: 2em;
  }

  .about-header {
    font-size: 2em;
  }

  .card-container {
    width: 85%;
    img {
      width: 170px;
    }
    .name {
      font-size: 1.4em;
    }
    .about-me {
      .text {
        padding: 1em;
        font-size: smaller;
      }
    }
  }

  .skills {
    svg {
      width: 135%;
      margin-left: -2.5em;
    }
  }
}
