@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}body{font-family:"Poppins",sans-serif;background-color:#f5f5f5}header{font-size:2.5em;padding-bottom:1em}.typewriter-container{text-align:center}.intro-h1{font-size:6em;font-weight:normal;margin-bottom:.5em}.intro-h2{font-size:3em;font-weight:normal;margin-bottom:.5em}.skills{display:flex;align-items:center;justify-content:center;width:100%;height:100%}.card-container{background-color:#fff;max-width:1920px;width:70%;height:auto;border-radius:14px;box-shadow:0px 10px 30px #16919c}.card-container .header-image{background-position:0px 0px;background-repeat:repeat;background-size:contain;text-align:center;border-top-left-radius:14px;border-top-right-radius:14px;padding:5px}.card-container img{margin:auto;width:250px;border:solid #48d1cc 4px;border-radius:50%;margin-top:50px}.card-container .name{font-weight:bold;font-size:2em;text-align:center;padding:10px}.card-container .social-container{display:flex;border-top:solid #48d1cc 1px;text-align:center}.card-container .about-me{border-top:solid #48d1cc 1px;text-align:center}.card-container .about-me .text{padding:1em;font-size:1.2em}.card-container .socials{padding:1em;flex:1 1}.card-container .details{display:flex;align-items:center;justify-content:center}.card-container ul{list-style-type:none;margin-bottom:1em;font-size:1.5em}@media screen and (max-width: 1000px){.intro-h1{font-size:4em}.intro-h2{font-size:2em}.card-container img{width:180px}.card-container .name{font-size:1.4em}.card-container ul{font-size:1em}.card-container .about-me .text{padding:1em;font-size:medium}.skills{flex-direction:column}.skills svg{width:135%;margin-left:-3em}}@media screen and (max-width: 400px){.intro-h1{font-size:3em}.intro-h2{font-size:2em}.about-header{font-size:2em}.card-container{width:85%}.card-container img{width:170px}.card-container .name{font-size:1.4em}.card-container .about-me .text{padding:1em;font-size:smaller}.skills svg{width:135%;margin-left:-2.5em}}
